@import 'variables';

@mixin sectionGap{
    position: relative;
    width: 100%;
    padding-top: 10vh;

    @media(min-width: 781px){
        padding-top: 15vh;
    }
}

@mixin sectionTitle{
    font-family: $special-font-family;
    margin-left: $margin-mobile;
    font-size: 2.3rem;

    @media (min-width: 781px){
        font-size: 3.5rem;
        margin-left: $margin-desktop
    }
}